#solr-pagination {
  margin: 1em 0;
}
#solr-pagination .pagination {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}
#solr-pagination .pagination li {
  font-size: 0.8em;
  min-width: 3.35em;
  height: 3.35em;
  box-sizing: border-box;
  margin: 0.165em;
}
@media (min-width: 40em) {
  #solr-pagination .pagination li {
    min-width: 2.65em;
    height: 2.65em;
    margin: 0.135em;
  }
}
#solr-pagination .pagination li a,
#solr-pagination .pagination li .number,
#solr-pagination .pagination li .spacer {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #050505;
  background: rgba(5, 5, 5, 0.1);
  border-radius: 3px;
  height: 100%;
  width: 100%;
  text-decoration: none;
}
#solr-pagination .pagination li a svg,
