}
@media (min-width: 60em) {
  #solr-pagination .pagination li a,
#solr-pagination .pagination li .number,
#solr-pagination .pagination li .spacer {
    background: transparent;
  }
}
#solr-pagination .pagination li a:hover {
  background: rgba(5, 5, 5, 0.1);
  text-decoration: none;
  color: inherit;
}
#solr-pagination .pagination li.active a,
#solr-pagination .pagination li.active .number {
  background: #333333;
  color: #fff;
}
@media (min-width: 60em) {
  #solr-pagination .pagination li.active a,
#solr-pagination .pagination li.active .number {
    background: #333333 !important;
  }
}

.search__form {
  transition-duration: 0.3s;
  display: flex;
  align-items: flex-end;
  position: relative;
  margin-bottom: 0.5em;
  font-size: 1em;
  color: rgba(5, 5, 5, 0.75);
  border-bottom: 3px solid #cccccc;
}
.search__form::after {
  transition: width 0.3s linear;
  position: absolute;
  top: 100%;
  display: block;
  width: 0;
  border-bottom: 3px solid #333333;
  content: "";
}
.search__form--focussed::after {
  width: 100%;
}
.search__input, .search__submit {
  appearance: none;
  padding: 0;
  color: inherit;
  border: 0;
  outline: 0;
  background: transparent;
  box-sizing: content-box;
}
.search__input {
  flex: 1 1 auto;
  min-width: 4.45em;
  padding-bottom: 0.1em;
  font-size: 1.5em;
  letter-spacing: 0.025em;
}
@media (min-width: 40em) {
  .search__input {
    font-size: 2em;
  }
}
.search__submit {
  transition-duration: inherit;
  cursor: pointer;
}
.search__submit:hover, .search__submit:focus {
  color: #333333;
}
.search__submit .icon {
  height: 1.7em;
  width: 1.7em;
  padding: 0.2em 0.5em;
  fill: currentColor;
}
@media (min-width: 40em) {
  .search__submit .icon {
    height: 2em;
    width: 2em;
  }
}

/**
 * Solr specific styles
 */
.results-highlight {
  background-color: rgba(5, 5, 5, 0.1);
}

